import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login'
import Admin from '../views/Admin'
import ResList from '../components/res/Reslist'
import Addres from '../components/res/Addres'
import Catelist from '../components/cate/Catelist'
import Addcate from '../components/cate/Addcate'
import Userlist from '../components/user/Userlist'
import Applylist from '../components/borrow/Applylist'
import Borrowlist from '../components/borrow/Borrowlist'
import Applyres from '../components/borrow/Applyres'
import Expired from '../components/borrow/Expired'
import BorrowRecord from '../components/record/BorrowRecord'
import ReturnRecord from '../components/record/ReturnRecord'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: { title: '' }
  },
  {
    path: '/',
    name: 'admin',
    component: Admin,
    children: [
      {
        path: 'expired',
        component: Expired,
        meta: { title: '' }
      },
      {
        path: 'reslist',
        component: ResList,
        props: true,
        meta: { title: '' }
      },
      {
        path: 'reslist/:id',
        component: ResList,
        props: true,
        meta: { title: '' }
      },
      {
        path: 'addres',
        meta: { title: '' },
        component: Addres
      },
      {
        path: 'addres/:id',
        meta: { title: '' },
        component: Addres,
        props: true
      },
      {
        path: 'catelist',
        meta: { title: '' },
        component: Catelist
      },
      {
        path: 'addcate/:id',
        meta: { title: '' },
        component: Addcate,
        props: true
      },
      {
        path: 'addcate',
        meta: { title: '' },
        component: Addcate,
        props: true
      },
      {
        path: 'userlist',
        meta: { title: '' },
        component: Userlist
      },
      {
        path: 'applylist',
        meta: { title: '' },
        component: Applylist
      },
      {
        path: 'borrowlist',
        meta: { title: '' },
        component: Borrowlist
      },
      {
        path: 'applyres/:id',
        meta: { title: '' },
        component: Applyres,
        props: true
      },
      {
        path: 'borrowrecord',
        component: BorrowRecord,
        meta: { title: '' },
        props: true
      },
      {
        path: 'returnrecord',
        component: ReturnRecord,
        meta: { title: '' },
        props: true
      }
    ]
  }
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})
router.beforeResolve((to, from, next) => {
  const token = window.sessionStorage.getItem('token')
  if (to.path === '/login') return next()
  if (!token && to.path !== '/login') {
    next('/login')
  } else {
    next()
  }
})

export default router
